body {
    background-color: black;
  }


  /* for ViewAllTodod file */
.Header1{
    color: rgb(249, 249, 250);
}

.center-table{
    margin: 0 auto;
    color: aliceblue;
  
    
}

.center-table td{
    text-align: center;
    background-color: rgb(33, 2, 43);
    
    
}

.center-table, th, td {
    border-radius: 10px;
}

.content1{
    margin-top: 50px;
    width: 750px;
    max-width: 1000px;
    height: 500px;
    background-color: rgba(137, 43, 226, 0.37);
    overflow-y: auto;
    border-radius: 40px;
    padding: 0 20px;
       
}

@media only screen and (max-width: 768px) {
    .content1 {
      width: 100%; /* Make the content take full width on smaller screens */
      height: auto; /* Allow the content to expand vertically */
    }
  }

/* end for viewalltodo */



